body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.users {
  margin: 15px;
}

.users div {
  display: -webkit-flex;
  display: flex;
  margin: 5px;
}

.users div img {
  height: 70px;
  margin-right: 5px;
}

.users div p {
  margin-right: 5px
}

.users div p.id {
  font-weight: bold;
}
input {
  margin-right: 5px;
}

.addUser {
  margin: 15px;
}
.app {}

.app button {
  margin: 15px;
}
